.container {
    display: flex;
    flex-direction: column;
}


.calculator-container {
    height: 100%;
    /* background: linear-gradient(to bottom, #903cc3e, #BE8CDC); */
    background: linear-gradient(to bottom, #903CC3EE, #BE8CDC);
    width: 90%;
    cursor: pointer;
    margin-right: 5%;
    border-radius: 6px;
    justify-content: center;
    margin-bottom: 2%;
}

.LP-amount {
    color: white;
    text-align: center;
    font-size: 65px;
    margin-top: 4%;
    margin-bottom: 8%;

}

.skill-controls {
    width: 90%;
}

.lp-controls {
    width: 90%;
    margin-bottom: 1%;
    margin-top: 2%;
}

.lp-text {
    width: 60%;
}

.lp-add {
    width: 20%;
}

.lp-subtract {
    width: 20%;
}

.lp-reset {
    width: 95%;
}